.App {
  text-align: center;
  background-color: #282c34;
  min-width: 100vh;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  justify-content: center;
  font-size: calc(50px + 2vmin);
  color: white;
}

h3{
  margin: 0px;
}
.App-header p{
  font-size: calc(15px + 2vmin);
}
/* Elements */
li {
  padding: 10px 0;
}

button {
  font-size: 30px;
  margin-right: 30px;
  color: white;
  background: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  float: right;
  padding-top: 20px;
}

/* Class */
.navBar {
  width: 100%;
  background-color:inherit;
  color: white;
  justify-content: flex-end;
  position: absolute;
  z-index: 1;
}

.links {
height: 0;
overflow: hidden;
list-style-type: none;
justify-content: flex-end;
}

.links li{
  padding: 10px;
}

.show-nav {
  height: 120px;
}

/* Media screen size */
@media screen and (min-width: 500px) {
  button {
      display: none;
  }

  .links {
      height: auto;
      display: flex;
  }

  a {
      padding: 0 15px;
  }
}

.test{
  background-color: #fff;
}

.card{
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  padding: 16px;
  text-align: center;
  background-color: #f1f1f1;
}

.cards{
  display: flex;
   flex-wrap: wrap;
   justify-content: center;
}

/* Float four columns side by side */
.column {
  float: left;
  width: 25%;
  padding: 0 10px;
}

/* Responsive columns */
@media screen and (max-width: 800px) {
  .column {
    width: 100vh;
    display: block;
    margin-bottom: 20px;
  }
  .navBar {
    width: 100vh;
  }
}

.particles {
  position: absolute;
  min-height: 100vh;
  min-width: 100vh;
  display: block;
}

canvas {
  min-height: 100vh;
  min-width: 100vh;
}